<template>
  <div class="slider">
    <div class="slider-img" @mouseover="clearInv" @mouseout="runInv">
      <transition-group name="slider">
      	<div v-for="(item, index) in slides" v-if="nowIndex === index" class="imgdiv" :key="index" style="overflow:hidden">
          <div class="container" style="height:438px;position:relative">
            <img
              :src="item.src"
              style="left:-360px"
            />
          </div>
        	<div class="btn-group">
        		<div class="container" style="padding-left:120px">
        			<div class="title">{{$t('m.sliderTitle')}}</div>
        			<div class="text">{{$t('m.sliderText')}}</div>
        			<div class="btn">{{$tc('m.download', 2)}}</div>
        			<div class="version">{{$tc('m.newVersion', 2)}}： v1.0125</div>
        		</div>
        	</div>
      	</div>
      </transition-group>
      <div class="slider-icon">
        <ul>
          <li v-for="(item, index) in slides" @click="goto(index)" @mouseover="goto(index)">
            <a :class="{ active: index === nowIndex }"></a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    slides: {
      type: Array,
      default: []
    },
    inv: {
      type: Number,
      default: 3000
    }
  },
  data () {
    return {
      nowIndex: 0
    }
  },
  watch: {
  },
  computed: {
    prevIndex () {
      if (this.nowIndex === 0) {
        return this.slides.length - 1
      } else {
        return this.nowIndex - 1
      }
    },
    nextIndex () {
      if (this.nowIndex === this.slides.length - 1) {
        return 0
      } else {
        return this.nowIndex + 1
      }
    }
  },
  methods: {
    goto (index) {
      this.nowIndex = index
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextIndex)
      }, this.inv)
    },
    clearInv () {
      clearInterval(this.invId)
    }
  },
  mounted () {
    this.runInv()
  }
}
</script>

<style scoped lang='stylus'>
@import '../../../common/stylus/mixin.styl'
&.slider-enter
  transform translateX(100%)
&.slider-enter-active, .slider-leave-active
  transition all 1.2s
&.slider-leave-to
  transform translateX(-100%)
.slider
  width 100%
  height 438px
  position relative
  overflow hidden
  .slider-img
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    .imgdiv
    	display inline-block
    	width 100%
	    height 100%
	    position absolute
	    top 0
	    img
		    position absolute
		  .btn-group
		  	position absolute
		  	top 0
		  	width 100%
		  	.title
		  		margin-top 110px
		  		font-family MicrosoftYaHei-Bold
					font-size 30px
					color #fff
				.text
					margin-top 24px 
					font-size 18px
				.btn
					display inline-block
					margin-top 86px
					cursor pointer
					font-size 18px
					padding 14px 44px
					border-radius: 4px;
					background-color: #ff600a;
				.version
					margin-top 16px
					font-size 14px
    .slider-icon
      width 100%
      position absolute
      bottom 20px
      left 0
      ul
        list-style none
        float left
        position relative
        left 50%
        li
          margin 0 10px
          float left
          position relative
          right 50%
          a
            display block
            width 8px
            height 8px
            border 1px solid #fff
            border-radius 50%
            background #fff
            vertical-align middle
            cursor pointer
          .active
            width 14px
            height 14px
            position relative
            top -3px
</style>



