<template>
  <div>
    <slider :slides="slides"></slider>
    <div class="detail">
      <div class="container wrapper">
        <img src="../../assets/mall/bg-thai-123.png" alt="">
        <div class="item1">
          <div class="title">{{$t('m.mallAdvantage')}}</div>
          <div class="text">{{$t('m.mallText')}}</div>
        </div>
        <div class="item2">
          <div class="title">{{$t('m.mallAdvantage1')}}</div>
          <div class="text">{{$t('m.mallText1')}}</div>
        </div>
        <div class="item3">
          <div class="title">{{$t('m.mallAdvantage2')}}</div>
          <div class="text">{{$t('m.mallText2')}}</div>
        </div>
      </div>
    </div>
    <div class="joinus">
      <div class="container text-center">
        <div class="text">{{$t('m.mallNumber')}}</div>
        <div class="number">1,0000+</div>
        <div class="text">{{$t('m.mallNumber2')}}</div>
      </div>
    </div>
    <div class="question-wrapper">
      <div class="container">
        <div class="title">
          <span>{{$t('m.normalQuestion')}}</span>
        </div>
        <div class="question">
          <div class="qtitle">
            1. 如何查询订单的物流状态？
          </div>
          <div class="answer">
            <p>商品发货后，买家在【我的订单】点击【已买到的宝贝】页面找到对应交易，点击【查看物流】即可查看到物流跟踪信息。如果暂时没有物流信息出现，可以通过ThaiPayMall消息功能联系客服咨询。</p>
            <div>
              常用的物流公司和官方网站如下：
              1） 圆通速递查询页面： http://www.yto.net.cn/gw/index/index.html <br>
              2） EMS查询页面： http://www.ems.com.cn/<br>
              3） 申通快递查询页面： http://www.sto.cn/<br>
              4） 中通速递查询页面： http://www.zto.cn/<br>
              5） 天天快递查询页面： http://www.ttkd.cn/<br>
              6） 韵达速递查询页面： http://www.yundaex.com/<br>
              查询更多物流公司网站可以点此链接： http://www.kiees.cn/default.htm
            </div>
          </div>
        </div>
        <div class="question">
          <div class="qtitle" style="margin-top:16px">
            2、只有学生可以分期付款吗？
          </div>
          <div class="answer">
            <p>是的，目前商城只有学生可以分期付款。如果开始对全社会开放分期权限，商城会有对应通知，敬请期待。</p>
          </div>
        </div>
        <div class="question">
          <div class="qtitle">
            3、如果商品不是正品怎么办？
          </div>
          <div class="answer">
            <p>如果不是正品，商品可免费为顾客退换货，请顾客放心。</p>
          </div>
        </div>
        <div class="question">
          <div class="qtitle" style="text-decoration:underline;cursor:pointer">
            更多问题请查看>>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Slider from './item/slider'
  export default {
    data () {
      return {
        slides: [
          {
            src: require('../../assets/home/pic-1.png')
          },
          {
            src: require('../../assets/mall/bg-tpm-01.png')
          }
        ]
      }
    },
    created () {
      window.scrollTo(0, 0)
    },
    components: {
      Slider
    }
  }
</script>
<style scoped lang='stylus'>
  @import '../../common/stylus/mixin.styl'
  .detail
    margin-top 96px
    margin-bottom 88px
    width 100%
    height 1826px
    overflow hidden
    .wrapper
      height 100%
      position relative
      background-position -360px -80px
      img
        position absolute
        left -360px
        top -80px
      .item1
        position absolute
        padding-top 196px
        margin-left 740px
        z-index 100
      .item2
        position absolute
        top: 870px;
        left: 130px;
      .item3
        position absolute
        bottom: 140px;
        margin-left 740px
  .joinus
    bg-image('../../assets/mall/bg-thai-part-5')
    width 100%
    height 276px
    padding-top 86px
    .text
      text-align center
      font-size 18px
    .number
      font-family: MicrosoftYaHei;
      font-size: 36px;
      font-weight: bold;
      text-align: center;
      color: #ff600a;
      margin 22px 0
  .question-wrapper
    padding 72px 0 116px 0
    .title
      font-family: MicrosoftYaHei;
      font-size: 16px;
      font-weight: bold;
      text-align: left;
      color: #555555;
      margin-bottom 32px
      span
        padding 0 4px 10px 4px
        border-bottom 3px solid #ff600a
    .question
      line-height 1.29
      .qtitle
        font-family: MicrosoftYaHei;
        font-size: 14px;
        font-weight: bold;
        line-height: 1.71;
        text-align: left;
        color: #555555;
      .answer
        p
          text-indent 2em
        div
          margin-left 2em
  .title
    font-family: MicrosoftYaHei;
    font-size: 36px;
    text-align: left;
    color: #ff600a;
    margin-bottom 24px
  .text
    font-family: MicrosoftYaHei;
    font-size: 16px;
    text-align: left;
    color: #555555;

</style>


